.modal-card,
.modal-content {
  width: 70%;
}

.dropzone-wrapper > div {
  display: -ms-flexbox;
  display: flex;
  padding: 15px;
  -ms-flex-align: center;
      align-items: center;
  width: 100% !important;
  text-align: center;
  cursor: pointer;
}

.is-margin-top {
  margin-top: 15px;
}

.upload-column {
  position: relative;
}

.mainImageLoading {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 1000;
  background: #fff
    url(//res.cloudinary.com/demo-robert/image/upload/v1452886398/loader.gif)
    no-repeat center;
  opacity: 0.7;
}


